import React from "react";

// reactstrap components
import {
    Card,
    CardBody,
    CardHeader,
    CardTitle,
    Table,
    Row,
    Col,
    Button,
    FormGroup,
    Form,
    Input,
    UncontrolledTooltip
} from "reactstrap";

// reactstrap
import {Route, Switch, Redirect} from "react-router-dom";

import {connect} from "react-redux";
import {getUser} from "../../actions/userActions";
import {addEvent} from "../../actions/eventActions";
import PropTypes from "prop-types";

// core components
import PanelHeader from "components/PanelHeader/PanelHeader.jsx";


class EventAdd extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            eventName:     "",
            eventStart:    "",
            eventEnd:      "",
            eventLocation: "",
            eventAge:      "",
            user:          "",
            isLoaded:      false
        }
    }

    componentWillMount() {
        this.props.getUser("5e8b3e31b1fb5d3ece24b2a9");
    }

    // set userdata
    componentDidUpdate() {
        if (!this.props.userData.loading && this.state.isLoaded === false) {
            this.setState({
                user:     this.props.userData.user._id,
                isLoaded: true,
            });
        }
    }

    /**
     * creates event
     */
    addEvent() {

        const {isLoaded, ...body} = this.state;

        this.props.addEvent(body)
    }

    /**
     * Handles input changes
     * @param e
     */
    handleChange(e) {
        const value = e.target.value;
        this.setState({
            ...this.state,
            [e.target.name]: value
        });
    }

    render() {

        if (this.props.userData.loading) {
            return (
                <p>Loading</p>
            )
        } else {

            return (
                <>
                    {console.log(this.state)}
                    <PanelHeader size="sm"/>
                    <div className="content">
                        <Row>

                            <Col xs={12} md={6}>
                                <Card>
                                    <div className="">
                                        <img alt="..." src={require("assets/img/events/placeholder.png")}/>
                                    </div>
                                    <CardBody>
                                        <div className="author">
                                            <h5 className="title">{this.state.eventName ? this.state.eventName : "Event Name"}</h5>
                                            <p className="description">{new Date(this.state.eventStart).toLocaleString() + " - " + new Date(this.state.eventEnd).toLocaleString()}</p>
                                        </div>
                                    </CardBody>
                                </Card>
                            </Col>

                            <Col xs={6}>
                                <Card>
                                    <CardHeader>
                                        <h5 className="title">Create Event</h5>
                                        <p className="category"> Create an event. Please note the information you fill
                                            in will be displayed on the distributed tickets.</p>
                                    </CardHeader>
                                    <CardBody>
                                        <Form>
                                            <Row>

                                                <Col md="12">
                                                    <FormGroup>
                                                        <label>Event name</label>
                                                        <Input
                                                            name="eventName"
                                                            placeholder="Event name"
                                                            type="text"
                                                            onChange={(e) => {
                                                                this.handleChange(e)
                                                            }}
                                                        />
                                                    </FormGroup>
                                                </Col>
                                                <Col md="12">
                                                    <FormGroup>
                                                        <label>
                                                            Event start
                                                        </label>
                                                        <Input
                                                            name="eventStart"
                                                            placeholder="Event start"
                                                            type="datetime-local"
                                                            onChange={(e) => {
                                                                this.handleChange(e)
                                                            }}
                                                        />
                                                    </FormGroup>
                                                </Col>
                                                <Col md="12">
                                                    <FormGroup>
                                                        <label>Event end</label>
                                                        <Input
                                                            name="eventEnd"
                                                            placeholder="Event end"
                                                            type="datetime-local"
                                                            onChange={(e) => {
                                                                this.handleChange(e)
                                                            }}
                                                        />
                                                    </FormGroup>
                                                </Col>
                                            </Row>
                                            <Row>
                                                <Col md="12">
                                                    <FormGroup>
                                                        <label>Location</label>
                                                        <Input
                                                            name="eventLocation"
                                                            placeholder="Location"
                                                            type="text"
                                                            onChange={(e) => {
                                                                this.handleChange(e)
                                                            }}
                                                        />
                                                    </FormGroup>
                                                </Col>
                                                <Col md="12">
                                                    <FormGroup>
                                                        <label>Minimum age</label>
                                                        <Input
                                                            name="eventAge"
                                                            placeholder="Minimum age"
                                                            type="number"
                                                            onChange={(e) => {
                                                                this.handleChange(e)
                                                            }}
                                                        />
                                                    </FormGroup>
                                                </Col>
                                            </Row>
                                            <button className="btn-round btn btn-success"
                                                    style={{fontSize: "1em", position: "relative", width: "100px"}}
                                                    onClick={(e) => {
                                                        e.preventDefault();
                                                        this.addEvent();

                                                        console.log(this.props.eventData.event._id)
                                                    }}>
                                                Create
                                            </button>
                                        </Form>
                                    </CardBody>
                                </Card>
                            </Col>
                        </Row>
                    </div>
                </>
            );
        }
    }
}

EventAdd.propTypes = {
    addEvent: PropTypes.func.isRequired,
    getUser:  PropTypes.func.isRequired,
    userData: PropTypes.object.isRequired,
    eventData: PropTypes.object.isRequired
};

const mapStateToProps = (state) => ({
    userData: state.userData,
    eventData: state.eventData,
});

export default connect(mapStateToProps, {getUser, addEvent})(EventAdd);
