import React from "react";

// reactstrap components
import {
    Card,
    CardBody,
    CardHeader,
    CardTitle,
    Table,
    Row,
    Col,
    Button,
    FormGroup,
    Form,
    Input,
    UncontrolledTooltip,
    CardFooter,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem
} from "reactstrap";

// reactstrap
import {Route, Switch, Redirect} from "react-router-dom";

import {connect} from "react-redux";
import {getUser} from "../../../actions/userActions";
import {addEvent, getEvent} from "../../../actions/eventActions";
import {updateTicketVariant, getTicketVariant} from "../../../actions/ticketVariantActions";
import PropTypes from "prop-types";

// core components
import PanelHeader from "components/PanelHeader/PanelHeader.jsx";
import {Bar, Line} from "react-chartjs-2";
import {LoadingScreenSpinner} from "../../../components/LoadingScreen/LoadingScreen";

import {
    dashboardAllProductsChart,
    dashboard24HoursPerformanceChart
} from "variables/charts.jsx";

class TicketVariantRead extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            _id:           "",
            description:   "",
            price:         "",
            availability:  "",
            personsAmount: "",
            event:         "",
            isLoaded:      false
        }
    }

    componentWillMount() {
        this.props.getUser("5e8b3e31b1fb5d3ece24b2a9");
        this.props.getTicketVariant(this.props.match.params.ticketVariantId);

        console.log(this.props.match.params.id);
        console.log(this.props.match.params.ticketVariantId);
    }

    // set ticketvariantdata
    componentDidUpdate() {
        if (!this.props.ticketVariantData.loading && this.state.isLoaded === false) {

            const {ticketVariants} = this.props.ticketVariantData;

            console.log(ticketVariants);

            this.setState({
                _id:           ticketVariants._id,
                description:   ticketVariants.description,
                price:         ticketVariants.price,
                availability:  true,
                personsAmount: ticketVariants.personsAmount,
                event:         this.props.match.params.id,
                isLoaded:      true,
            });
        }
    }

    updateTicketVariant() {
        const {isLoaded, ...body} = this.state;

        this.props.updateTicketVariant(this.state._id, body)
    }

    /**
     * Handles input changes
     * @param e
     */
    handleChange(e) {
        const value = e.target.value;
        this.setState({
            ...this.state,
            [e.target.name]: value
        });
    }

    render() {

        if (this.props.userData.loading && this.props.ticketVariantData.loading) {
            return (
                <LoadingScreenSpinner/>
            )
        } else {
            return (
                <>
                    {console.log(this.state)}
                    <PanelHeader size="sm"/>
                    <div className="content">
                        <Row>
                            <Col xs={12} md={6}>
                                <Card>

                                    <CardHeader>
                                        <div>
                                            <h5 className="title">Ticket Variant: {this.state.description}</h5>
                                        </div>
                                    </CardHeader>

                                    <hr/>

                                    <CardHeader>
                                        <CardTitle tag="h4">Total Orders</CardTitle>
                                        <p className="category">The graph displays total orders</p>
                                    </CardHeader>

                                    <CardBody>
                                        <div className="chart-area">
                                            <Line
                                                data={dashboardAllProductsChart.data}
                                                options={dashboardAllProductsChart.options}
                                            />
                                        </div>
                                    </CardBody>
                                </Card>
                            </Col>

                            <Col xs={12} md={6}>
                                <Card>
                                    <CardHeader>
                                        <CardTitle tag="h4">Update</CardTitle>
                                        <p className="category">Update the ticket variant here.</p>
                                    </CardHeader>

                                    <CardBody>
                                        <Form>
                                            <Row>
                                                <Col md="12">
                                                    <FormGroup>
                                                        <label>
                                                            Description
                                                        </label>
                                                        <Input
                                                            name="description"
                                                            placeholder="Description"
                                                            value={this.state.description}
                                                            onChange={(e) => {
                                                                this.handleChange(e)
                                                            }}
                                                        />
                                                    </FormGroup>
                                                </Col>
                                                <Col md="12">
                                                    <FormGroup>
                                                        <label>Price (EUR)</label>
                                                        <Input
                                                            name="price"
                                                            placeholder="Price"
                                                            value={this.state.price}
                                                            onChange={(e) => {
                                                                this.handleChange(e)
                                                            }}
                                                        />
                                                    </FormGroup>
                                                </Col>
                                                <Col md="12">
                                                    <FormGroup>
                                                        <label>Persons allowed per ticket</label>
                                                        <Input
                                                            name="personsAmount"
                                                            placeholder="Amount"
                                                            value={this.state.personsAmount}
                                                            type="number"
                                                            onChange={(e) => {
                                                                this.handleChange(e)
                                                            }}
                                                        />
                                                    </FormGroup>
                                                </Col>
                                            </Row>
                                            <button className="btn-round btn btn-success"
                                                    style={{fontSize: "1em", position: "relative", width: "100px"}}
                                                    onClick={(e) => {
                                                        e.preventDefault();
                                                        this.updateTicketVariant()
                                                    }}>
                                                Update
                                            </button>
                                        </Form>
                                    </CardBody>
                                </Card>
                            </Col>


                            <Col xs={12}>
                                <Card className="card-plain">
                                    <CardHeader>
                                        <CardTitle tag="h4">Sales</CardTitle>
                                        <p className="category"> All the sales for this ticket variant</p>
                                    </CardHeader>
                                    <CardBody>
                                        <Table responsive>
                                            <thead>
                                            <tr>
                                                <th>Order ID</th>
                                                <th>Amount</th>
                                                <th>Method</th>
                                                <th>Status</th>
                                                <th>Date</th>
                                                <th className="text-center"> Details</th>
                                            </tr>
                                            </thead>
                                            <tbody>
                                            <tr>
                                                <td>test</td>
                                                <td>test</td>
                                                <td>test</td>
                                                <td>test</td>
                                                <td>test</td>
                                                <td className="text-center">
                                                    <button type="button" aria-haspopup="true" aria-expanded="false"
                                                            className="btn-round btn-outline-default btn-icon btn btn-default"
                                                            style={{margin: "0px 5px"}}>
                                                        <i className="now-ui-icons ui-1_zoom-bold"></i>
                                                    </button>

                                                </td>
                                            </tr>
                                            </tbody>
                                        </Table>
                                    </CardBody>
                                </Card>
                            </Col>
                        </Row>
                    </div>
                </>
            );
        }
    }
}

TicketVariantRead.propTypes = {
    getEvent:            PropTypes.func.isRequired,
    addEvent:            PropTypes.func.isRequired,
    getTicketVariant:    PropTypes.func.isRequired,
    updateTicketVariant: PropTypes.func.isRequired,
    getUser:             PropTypes.func.isRequired,
    userData:            PropTypes.object.isRequired,
    eventData:           PropTypes.object.isRequired,
    ticketVariantData:   PropTypes.object.isRequired
};

const mapStateToProps = (state) => ({
    userData:          state.userData,
    eventData:         state.eventData,
    ticketVariantData: state.ticketVariantData,
});

export default connect(mapStateToProps, {
    getUser,
    addEvent,
    getEvent,
    updateTicketVariant,
    getTicketVariant
})(TicketVariantRead);
