import axios from "axios";
import {GET_TICKET_VARIANTS, GET_TICKET_VARIANT, ADD_TICKET_VARIANT, UPDATE_TICKET_VARIANT, DELETE_TICKET_VARIANT, LOADING_TICKET_VARIANT} from "./types";

export const getTicketVariants = () => dispatch => {
    dispatch(setTicketVariantLoading());
    axios.get("/ticketvariants/").then(res =>
        dispatch({
            type:    GET_TICKET_VARIANTS,
            payload: res.data
        }))
};

export const getTicketVariant = (id) => dispatch => {
    dispatch(setTicketVariantLoading());
    axios.get("/ticketvariants/" + id).then(res =>
        dispatch({
            type:    GET_TICKET_VARIANT,
            payload: res.data[0]
        }))
};

export const addTicketVariant = (body) => dispatch => {
    dispatch(setTicketVariantLoading());
    axios.post("/ticketvariants/add", body).then(res =>
        dispatch({
            type:    ADD_TICKET_VARIANT,
            payload: res.data
        })
    )

};

export const updateTicketVariant = (id, body) => dispatch => {
    dispatch(setTicketVariantLoading());
    axios.post("/ticketvariants/update/" + id, body).then(res =>
        dispatch({
            type:    UPDATE_TICKET_VARIANT,
            payload: res.data
        }))
         .catch(err => {
             console.log(err);
         })
};

export const deleteTicketVariant = (id) => dispatch => {
    dispatch(setTicketVariantLoading());
    axios.delete("/ticketvariants/" + id).then(res =>
        dispatch({
            type:    DELETE_TICKET_VARIANT,
            payload: res.data[0]
        }))
};

export const setTicketVariantLoading = () => {
    return {
        type: LOADING_TICKET_VARIANT
    }
};
