import axios from "axios";
import {LOGIN_AUTH, LOGOUT_AUTH, MAIL_AUTH, VERIFY_AUTH, LOADING_AUTH} from "./types";

export const loginAuth = (body) => dispatch => {
    dispatch(setAuthLoading());

    axios.post("/users/signin", body).then(res => {

        mailAuth(body.userEmail, res.data.token);

        let mailBody = {
            userEmail: body.userEmail,
            token: res.data.token
        };

        dispatch(mailAuth(mailBody));

        dispatch({
            type:    LOGIN_AUTH,
            payload: res.data
        })
    })
};

export const logoutAuth = (token) => dispatch => {
    dispatch(setAuthLoading());

    axios.post("/users/logout?token=" + token).then(res => {

        localStorage.removeItem("tickit-token");

        dispatch({
            type:    LOGOUT_AUTH,
            payload: res.data
        })
    })
};

export const mailAuth = (body) => dispatch => {
    axios.post("/mailer/auth", body).then(res => {
        dispatch({
            type:    MAIL_AUTH,
            payload: res.data
        })
    })
};

export const verifyAuth = (token) => dispatch => {
    axios.post("/users/verify?token=" + token).then(res => {
        dispatch({
            type:    VERIFY_AUTH,
            payload: res.data[0]
        });
    })
};

export const setAuthLoading = () => {
    return {
        type: LOADING_AUTH
    }
};
