import axios from "axios";
import {GET_EVENTS, GET_EVENT, ADD_EVENT, UPDATE_EVENT, DELETE_EVENT, LOADING_EVENT} from "./types";

export const getEvents = () => dispatch => {
    dispatch(setEventLoading());
    axios.get("/events/").then(res =>
        dispatch({
            type:    GET_EVENTS,
            payload: res.data
        }))
};

export const getEvent = (id) => dispatch => {
    dispatch(setEventLoading());
    axios.get("/events/" + id).then(res =>
        dispatch({
            type:    GET_EVENT,
            payload: res.data[0]
        }))
};

export const addEvent = (body) => dispatch => {
    dispatch(setEventLoading());
    axios.post("/events/add", body).then(res =>
        dispatch({
            type:    ADD_EVENT,
            payload: res.data
        })
    )

};

export const updateEvent = (id, body) => dispatch => {
    dispatch(setEventLoading());
    axios.post("/events/update/" + id, body).then(res =>
        dispatch({
            type:    UPDATE_EVENT,
            payload: res.data
        }))
         .catch(err => {
             console.log(err);
         })
};

export const deleteEvent = (id) => dispatch => {
    dispatch(setEventLoading());
    axios.delete("/events/" + id).then(res =>
        dispatch({
            type:    DELETE_EVENT,
            payload: res.data[0]
        }))
};

export const setEventLoading = () => {
    return {
        type: LOADING_EVENT
    }
};
