import React from "react";

// reactstrap components
import {
    Button,
    Card,
    CardHeader,
    CardBody,
    FormGroup,
    Form,
    Input,
    Row,
    Col
} from "reactstrap";

// components
import PanelHeader from "components/PanelHeader/PanelHeader.jsx";
import {NotificationContainer, SuccessNotification} from "../../components/Notifications/Notifications";

// redux/api
import {connect} from "react-redux";
import {getUser, updateUser} from "../../actions/userActions";
import {verifyAuth} from "../../actions/authActions";
import PropTypes from "prop-types";


/**
 * Class Component for User settings
 */
class User extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            userName:         "",
            userEmail:        "",
            firstName:        "",
            lastName:         "",
            address:          "",
            city:             "",
            country:          "",
            zipCode:          "",
            mollieKey:        "",
            mollieCustomerId: "",
            role:             "",
            isLoaded:         false,
        }
    }

    componentDidMount() {
        this.props.verifyAuth(localStorage.getItem("tickit-token"));
        this.props.getUser("5e8b3e31b1fb5d3ece24b2a9");
    }

    // set event data
    componentDidUpdate() {
        if (!this.props.userData.loading && this.state.isLoaded === false) {

            const {user} = this.props.userData;

            this.setState({
                userName:         user.userName || "",
                userEmail:        user.userEmail || "",
                firstName:        user.firstName || "",
                lastName:         user.lastName || "",
                address:          user.address || "",
                city:             user.city || "",
                country:          user.country || "",
                zipCode:          user.zipCode || "",
                mollieKey:        user.mollieKey || "",
                mollieCustomerId: user.mollieCustomerId || "",
                role:             user.role || "",
                isLoaded:         true,
            });
        }
    }

    /**
     * Updates user
     */
    updateUser() {
        const {isLoaded, role, ...body} = this.state;
        this.props.updateUser("5e8b3e31b1fb5d3ece24b2a9", body);
        SuccessNotification("User has been updated.");
    }

    /**
     * Handles input changes
     * @param e
     */
    handleChange(e) {
        const value = e.target.value;
        this.setState({
            ...this.state,
            [e.target.name]: value
        });
    }

    render() {
        const {user} = this.props.userData;

        return (
            <>
                <PanelHeader size="sm"/>
                <NotificationContainer/>

                <div className="content">
                    <Row>
                        <Col md="4">
                            <Card className="card-user">
                                <div className="image"
                                     style={{background: "linear-gradient(160deg, #F96332 0%, #F37C2B 100%)"}}>
                                </div>
                                <CardBody>
                                    <div className="author">
                                        <a href="#" onClick={e => e.preventDefault()}>
                                            <img
                                                alt="..."
                                                className="avatar border-gray"
                                                src={require("assets/img/user-male.png")}
                                            />
                                            <h5 className="title">{this.state.firstName + " " + this.state.lastName}</h5>
                                        </a>
                                        <p className="description">{this.state.userEmail}</p>
                                    </div>
                                </CardBody>
                                <hr/>
                                <div className="button-container">
                                    <Button
                                        className="btn-neutral btn-icon btn-round"
                                        color="default"
                                        href="#pablo"
                                        onClick={e => e.preventDefault()}
                                        size="lg"
                                    >
                                        <i className="fab fa-facebook-f"/>
                                    </Button>
                                    <Button
                                        className="btn-neutral btn-icon btn-round"
                                        color="default"
                                        href="#pablo"
                                        onClick={e => e.preventDefault()}
                                        size="lg"
                                    >
                                        <i className="fab fa-twitter"/>
                                    </Button>
                                    <Button
                                        className="btn-neutral btn-icon btn-round"
                                        color="default"
                                        href="#pablo"
                                        onClick={e => e.preventDefault()}
                                        size="lg"
                                    >
                                        <i className="fab fa-google-plus-g"/>
                                    </Button>
                                </div>
                            </Card>
                        </Col>
                        <Col md="8">
                            <Card>
                                <CardHeader>
                                    <h5 className="title">Edit Profile</h5>
                                </CardHeader>
                                <CardBody>
                                    <Form>
                                        <Row>
                                            <Col md="3">
                                                <FormGroup>
                                                    <label>Username</label>
                                                    <Input
                                                        name="userName"
                                                        defaultValue={this.state.userName}
                                                        placeholder="Username"
                                                        type="text"
                                                        onChange={(e) => {
                                                            this.handleChange(e)
                                                        }}
                                                    />
                                                </FormGroup>
                                            </Col>
                                            <Col md="4">
                                                <FormGroup>
                                                    <label>
                                                        Email address
                                                    </label>
                                                    <Input
                                                        name="userEmail"
                                                        defaultValue={this.state.userEmail}
                                                        placeholder="Email" type="email"
                                                        onChange={(e) => {
                                                            this.handleChange(e)
                                                        }}
                                                    />
                                                </FormGroup>
                                            </Col>
                                            <Col md="5">
                                                <FormGroup>
                                                    <label>Subscription</label>
                                                    <Input
                                                        defaultValue="Free"
                                                        disabled
                                                        placeholder="Company"
                                                        type="text"
                                                        onChange={(e) => {
                                                            this.handleChange(e)
                                                        }}
                                                    />
                                                </FormGroup>
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col md="6">
                                                <FormGroup>
                                                    <label>First Name</label>
                                                    <Input
                                                        name="firstName"
                                                        defaultValue={this.state.firstName}
                                                        placeholder="Company"
                                                        type="text"
                                                        onChange={(e) => {
                                                            this.handleChange(e)
                                                        }}
                                                    />
                                                </FormGroup>
                                            </Col>
                                            <Col md="6">
                                                <FormGroup>
                                                    <label>Last Name</label>
                                                    <Input
                                                        name="lastName"
                                                        defaultValue={this.state.lastName}
                                                        placeholder="Last Name"
                                                        type="text"
                                                        onChange={(e) => {
                                                            this.handleChange(e)
                                                        }}
                                                    />
                                                </FormGroup>
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col md="12">
                                                <FormGroup>
                                                    <label>Address</label>
                                                    <Input
                                                        name="address"
                                                        defaultValue={this.state.address}
                                                        placeholder="Home Address"
                                                        type="text"
                                                        onChange={(e) => {
                                                            this.handleChange(e)
                                                        }}
                                                    />
                                                </FormGroup>
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col md="4">
                                                <FormGroup>
                                                    <label>City</label>
                                                    <Input
                                                        name="city"
                                                        defaultValue={this.state.city}
                                                        placeholder="City"
                                                        type="text"
                                                        onChange={(e) => {
                                                            this.handleChange(e)
                                                        }}
                                                    />
                                                </FormGroup>
                                            </Col>
                                            <Col md="4">
                                                <FormGroup>
                                                    <label>Country</label>
                                                    <Input
                                                        name="country"
                                                        defaultValue={this.state.country}
                                                        placeholder="Country"
                                                        type="text"
                                                        onChange={(e) => {
                                                            this.handleChange(e)
                                                        }}
                                                    />
                                                </FormGroup>
                                            </Col>
                                            <Col md="4">
                                                <FormGroup>
                                                    <label>Postal Code</label>
                                                    <Input
                                                        name="zipCode"
                                                        defaultValue={this.state.zipCode}
                                                        placeholder="Postal Code"
                                                        type="text"
                                                        onChange={(e) => {
                                                            this.handleChange(e)
                                                        }}
                                                    />
                                                </FormGroup>
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col md="12">
                                                <FormGroup>
                                                    <label>Mollie API Key</label>
                                                    <Input
                                                        name="mollieKey"
                                                        defaultValue={this.state.mollieKey}
                                                        placeholder="Mollie Key"
                                                        onChange={(e) => {
                                                            this.handleChange(e)
                                                        }}
                                                    />
                                                </FormGroup>
                                            </Col>
                                        </Row>
                                        <button className="btn-round btn btn-success"
                                                style={{fontSize: "1.2em"}}
                                                onClick={(e) => {
                                                    e.preventDefault();
                                                    this.updateUser();
                                                }}>
                                            Save
                                        </button>
                                    </Form>
                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </div>
            </>
        );
    }
}

User.propTypes = {
    verifyAuth:    PropTypes.func.isRequired,
    getUser:    PropTypes.func.isRequired,
    updateUser: PropTypes.func.isRequired,
    userData:   PropTypes.object.isRequired
};

const mapStateToProps = (state) => ({
    userData: state.userData,
    authData: state.authData,
});

export default connect(mapStateToProps, {getUser, updateUser, verifyAuth})(User);
