import React, {Component} from "react";
import {hist} from "../../App";

import {connect} from "react-redux";
import {loginAuth, logoutAuth, verifyAuth} from "../../actions/authActions";
import PropTypes from "prop-types";

// QR code generator
import {QRCode} from "react-qr-svg";


class FixedPlugin extends Component {
    constructor(props) {
        super(props);
        this.state       = {
            classes: "dropdown"
        };
        this.handleClick = this.handleClick.bind(this);
    }

    /**
     * Handles dropdown
     */
    handleClick() {
        if (this.state.classes === "dropdown") {
            this.setState({classes: "dropdown show"});
        } else {
            this.setState({classes: "dropdown"});
        }
    }


    render() {
        return (
            <div className="fixed-plugin">
                <div className={this.state.classes}>
                    <div onClick={this.handleClick}>
                        <i className="fa fa-cog fa-2x"/>
                    </div>
                    <ul className="dropdown-menu show">
                        <li className="header-title">Actions</li>

                        <li className="button-container">
                            <a
                                href={"../account"}
                                className="btn btn-primary btn-block btn-round"
                                onClick={() => {
                                    this.props.logoutAuth(localStorage.getItem('tickit-token'));
                                }}
                            >
                                Logout
                            </a>

                            <a
                                href="#"
                                target="_blank"
                                className="btn btn-warning btn-block btn-round"
                            >
                                Download Android app
                            </a>
                            <a
                                href="https://www.creative-tim.com/product/now-ui-dashboard-react?ref=nudr-fixed-plugin"
                                target="_blank"
                                className="btn btn-warning btn-block btn-round disabled"
                            >
                                Download IOS app
                            </a>
                            <a
                                href="https://saifrashed.nl"
                                className="btn btn-block btn-round btn-info"
                            >
                                Contact
                            </a>

                            <p>Scan the QR code in the app to get access.</p>

                            <QRCode
                                value={"test"}
                            />
                        </li>
                    </ul>
                </div>
            </div>
        );
    }
}

FixedPlugin.propTypes = {
    logoutAuth: PropTypes.func.isRequired,
    authData:   PropTypes.object.isRequired
};

const mapStateToProps = (state) => ({
    authData: state.authData,
});

export default connect(mapStateToProps, {logoutAuth})(FixedPlugin);
