import React from "react";

import {Redirect} from "react-router";

import {connect} from "react-redux";
import {loginAuth, verifyAuth} from "../../actions/authActions";
import PropTypes from "prop-types";

import {getUser} from "../../actions/userActions";
import {addEvent, deleteEvent, getEvent, updateEvent} from "../../actions/eventActions";

import {NotificationContainer, SuccessNotification, WarningNotification} from "../../components/Notifications/Notifications";

/**
 * Component used to sign a user in.
 */
class UserSignIn extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            userEmail: ''
        };
    }

    componentWillMount() {

        var url   = new URL(window.location.href);
        var token = url.searchParams.get("token");

        if(token) {
            localStorage.setItem('tickit-token', token);
        }

        if (localStorage.getItem('tickit-token') !== "null" || !localStorage.getItem('tickit-token')) {
            this.props.verifyAuth(localStorage.getItem('tickit-token'));
        }


        console.log(this.props.authData.storageSession);

    }

    loginAuth() {
        const {...body} = this.state;
        this.props.loginAuth(body);

        if(this.state.userEmail === "") {
            WarningNotification("Email is empty");
        } else {
            SuccessNotification("A mail has been sent to login");
        }
    }

    /**
     * Handles input changes
     * @param e
     */
    handleChange(e) {
        const value = e.target.value;
        this.setState({
            ...this.state,
            [e.target.name]: value
        });
    }


    render() {

        if(this.props.authData.authVerified) {
            return <Redirect to={"/admin"} />
        }

        return (
            <div className="auth">
                <NotificationContainer/>

                <div className="container-fluid">
                    <div className="row no-gutter">
                        <div className="d-none d-md-flex col-md-4 col-lg-6 bg-image"></div>
                        <div className="col-md-8 col-lg-6">
                            <div className="login d-flex align-items-center py-5">
                                <div className="container">
                                    <div className="row">
                                        <div className="col-md-9 col-lg-8 mx-auto">
                                            <div className="text-center">
                                                <img src={require("../../assets/img/tickit_logo.png")}
                                                     style={{width: "300px", marginBottom: "50px"}}/>
                                                <h3 className="login-heading mb-4">Welcome back!</h3>
                                                <p>
                                                    We don't like passwords at TickIT. After filling your mail you will
                                                    be
                                                    send a mail with a link that will sign you in. Thank you!
                                                </p>
                                            </div>

                                            <br/>
                                            <form>
                                                <div className="form-label-group">
                                                    <input
                                                        name="userEmail"
                                                        type="email"
                                                        id="inputEmail"
                                                        className="form-control"
                                                        placeholder="Email address"
                                                        required autoFocus
                                                        onChange={(e) => {
                                                            this.handleChange(e)
                                                        }}/>
                                                </div>


                                                <div className="row no-gutter">
                                                    <div className="col-md-5">
                                                        <a
                                                            href={"../admin"}
                                                            className="btn btn-lg btn-primary btn-block btn-login text-uppercase font-weight-bold mb-2"
                                                            onClick={(e) => {
                                                                e.preventDefault();
                                                                this.loginAuth();
                                                            }}>
                                                            Sign in
                                                        </a>
                                                    </div>

                                                    <div className="col-md-2">
                                                        <div className="text-center" style={{padding: "1.25rem 0"}}>
                                                            Or

                                                        </div>
                                                    </div>

                                                    <div className="col-md-5">
                                                        <a
                                                            href={"./signup"}
                                                            className="btn btn-lg btn-primary btn-block btn-login text-uppercase font-weight-bold mb-2"
                                                        >Sign Up
                                                        </a>
                                                    </div>
                                                </div>


                                                <div className="text-center">
                                                    <a className="small" href="#">Forgot password?</a></div>
                                            </form>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}


UserSignIn.propTypes = {
    loginAuth:  PropTypes.func.isRequired,
    verifyAuth: PropTypes.func.isRequired,
    authData:   PropTypes.object.isRequired
};

const mapStateToProps = (state) => ({
    authData: state.authData,
});

export default connect(mapStateToProps, {loginAuth, verifyAuth})(UserSignIn);
