import React from "react";

// reactstrap components
import {
    Card,
    CardBody,
    CardHeader,
    CardTitle,
    Table,
    Row,
    Col,
    Button,
    UncontrolledTooltip
} from "reactstrap";


// reactstrap
import {Route, Switch, Redirect} from "react-router-dom";

import {connect} from "react-redux";
import {getUser} from "../../actions/userActions";
import {deleteEvent} from "../../actions/eventActions";
import PropTypes from "prop-types";

// core components
import PanelHeader from "components/PanelHeader/PanelHeader.jsx";
import {LoadingScreen} from "../../components/LoadingScreen/LoadingScreen";

class EventList extends React.Component {

    componentWillMount() {
        this.props.getUser("5e8b3e31b1fb5d3ece24b2a9");
    }

    render() {
        const {user} = this.props.userData;

        if (this.props.userData.loading) {
            return (
                <LoadingScreen title={"Events"} description={"All ur planned events."} message={"No events found"}/>
            )
        } else {
            return (
                <>
                    <PanelHeader size="sm"/>
                    <div className="content">
                        <Row>
                            <Col xs={12}>
                                <Card>
                                    <CardHeader>
                                        <CardTitle tag="h4">Events</CardTitle>
                                        <p className="category"> All ur planned events.</p>
                                    </CardHeader>
                                    <CardBody>
                                        <Table responsive>
                                            <thead className="text-primary">
                                            <tr>
                                                <th>Name</th>
                                                <th>Start</th>
                                                <th>End</th>
                                                <th>Location</th>
                                                <th className="text-center">Actions</th>
                                            </tr>
                                            </thead>
                                            <tbody>
                                            {user.events ? user.events.map((value, index) => (
                                                <tr key={index}>
                                                    <td>{value.eventName}</td>
                                                    <td>{new Date(value.eventStart).toLocaleString()}</td>
                                                    <td>{new Date(value.eventEnd).toLocaleString()}</td>
                                                    <td>{value.eventLocation}</td>
                                                    <td className="text-center">
                                                        <a href={"./event/" + value._id}
                                                           className="btn-round btn-outline-default btn-icon btn btn-default"
                                                           style={{margin: "0px 5px"}}>
                                                            <i className="now-ui-icons loader_gear"></i>
                                                        </a>

                                                        <a href="#"
                                                           className="btn-round btn-outline-primary btn-icon btn btn-default"
                                                           style={{margin: "0px 5px"}} onClick={(e) => {
                                                            e.preventDefault();
                                                            this.props.deleteEvent(value._id);
                                                            this.props.getUser("5e8b3e31b1fb5d3ece24b2a9");
                                                        }}>
                                                            <i className="now-ui-icons ui-1_simple-remove"></i>
                                                        </a>
                                                    </td>
                                                </tr>
                                            )) : "No events found"}

                                            <tr>
                                                <td colSpan="4"></td>
                                                <td className="text-center">
                                                    <a href="#"
                                                       className="btn-round btn-outline-default btn-icon btn btn-default"
                                                       style={{margin: "0px 5px"}} onClick={(e) => {
                                                        e.preventDefault();
                                                        this.props.getUser("5e8b3e31b1fb5d3ece24b2a9");
                                                    }}>
                                                        <i className="now-ui-icons loader_refresh"></i>
                                                    </a>
                                                    <a href="./event/create"
                                                       className="btn-round btn-outline-success btn-icon btn btn-default"
                                                       style={{margin: "0px 5px"}}>
                                                        <i className="now-ui-icons ui-1_simple-add"></i>
                                                    </a>
                                                </td>
                                            </tr>
                                            </tbody>
                                        </Table>
                                    </CardBody>
                                </Card>
                            </Col>
                        </Row>
                    </div>
                </>
            );
        }
    }
}

EventList.propTypes = {
    deleteEvent: PropTypes.func.isRequired,
    getUser:     PropTypes.func.isRequired,
    userData:    PropTypes.object.isRequired
};

const mapStateToProps = (state) => ({
    userData: state.userData,
});

export default connect(mapStateToProps, {getUser, deleteEvent})(EventList);
