import React from "react";

// reactstrap components
import {
    Card,
    CardBody,
    CardHeader,
    CardTitle,
    Table,
    Row,
    Col,
    Button,
    FormGroup,
    Form,
    Input,
    UncontrolledTooltip,
    CardFooter,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem
} from "reactstrap";

// reactstrap
import {Route, Switch, Redirect} from "react-router-dom";

import {connect} from "react-redux";
import {getUser} from "../../../actions/userActions";
import {updateTicketVariant, getTicketVariant, addTicketVariant} from "../../../actions/ticketVariantActions";
import PropTypes from "prop-types";

// core components
import PanelHeader from "components/PanelHeader/PanelHeader.jsx";
import {Bar, Line} from "react-chartjs-2";

import {
    dashboardAllProductsChart,
    dashboard24HoursPerformanceChart
} from "variables/charts.jsx";

class TicketVariantAdd extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            description:   "",
            price:         "",
            availability:  "",
            personsAmount: "",
            event:         "",
            user:          "",
            isLoaded:      false
        }
    }

    componentWillMount() {
        this.props.getUser("5e8b3e31b1fb5d3ece24b2a9");

        console.log(this.props.match.params.id)
    }

    // set ticketvariantdata
    componentDidUpdate() {
        if (!this.props.userData.loading && this.state.isLoaded === false) {
            this.setState({
                availability: true,
                event:        this.props.match.params.id,
                user:                this.props.userData.user._id,
                isLoaded:            true,
            });
        }
    }

    /**
     * creates ticket variant
     */
    addTicketVariant() {

        const {isLoaded, ...body} = this.state;

        this.props.addTicketVariant(body)
    }

    /**
     * Handles input changes
     * @param e
     */
    handleChange(e) {
        const value = e.target.value;
        this.setState({
            ...this.state,
            [e.target.name]: value
        });
    }

    render() {

        if (this.props.userData.loading) {
            return (
                <p>Loading</p>
            )
        } else {
            return (
                <>
                    {console.log(this.state)}
                    <PanelHeader size="sm"/>
                    <div className="content">
                        <Row>
                            <Col xs={12} md={6}>
                                <Card>
                                    <CardHeader>
                                        <CardTitle tag="h4">Add ticket variant</CardTitle>
                                        <p className="category">add a ticket variant here.</p>
                                    </CardHeader>

                                    <CardBody>
                                        <Form>
                                            <Row>
                                                <Col md="12">
                                                    <FormGroup>
                                                        <label>
                                                            Description
                                                        </label>
                                                        <Input
                                                            name="description"
                                                            placeholder="Description"
                                                            onChange={(e) => {
                                                                this.handleChange(e)
                                                            }}
                                                        />
                                                    </FormGroup>
                                                </Col>
                                                <Col md="12">
                                                    <FormGroup>
                                                        <label>Price (EUR)</label>
                                                        <Input
                                                            name="price"
                                                            placeholder="Price"
                                                            onChange={(e) => {
                                                                this.handleChange(e)
                                                            }}
                                                        />
                                                    </FormGroup>
                                                </Col>
                                                <Col md="12">
                                                    <FormGroup>
                                                        <label>Persons allowed per ticket</label>
                                                        <Input
                                                            name="personsAmount"
                                                            placeholder="Amount"
                                                            type="number"
                                                            onChange={(e) => {
                                                                this.handleChange(e)
                                                            }}
                                                        />
                                                    </FormGroup>
                                                </Col>
                                            </Row>
                                            <button className="btn-round btn btn-success"
                                                    style={{fontSize: "1em", position: "relative", width: "100px"}}
                                                    onClick={(e) => {
                                                        e.preventDefault();
                                                        this.addTicketVariant();
                                                    }}>
                                                Create
                                            </button>
                                        </Form>
                                    </CardBody>
                                </Card>
                            </Col>
                        </Row>
                    </div>
                </>
            );
        }
    }
}

TicketVariantAdd.propTypes = {
    getUser:           PropTypes.func.isRequired,
    addTicketVariant:  PropTypes.func.isRequired,
    userData:          PropTypes.object.isRequired,
    eventData:         PropTypes.object.isRequired,
    ticketVariantData: PropTypes.object.isRequired
};

const mapStateToProps = (state) => ({
    userData:          state.userData,
    eventData:         state.eventData,
    ticketVariantData: state.ticketVariantData,
});

export default connect(mapStateToProps, {
    getUser,
    updateTicketVariant,
    addTicketVariant,
})(TicketVariantAdd);
