import React from "react";

// reactstrap components
import {
    Card,
    CardBody,
    CardHeader,
    CardTitle,
    Table,
    Row,
    Col,
    Button,
    FormGroup,
    Form,
    Input,
    UncontrolledTooltip,
    CardFooter,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem
} from "reactstrap";

// reactstrap
import {Route, Switch, Redirect} from "react-router-dom";

import {connect} from "react-redux";
import {getUser} from "../../actions/userActions";
import {addEvent, getEvent, updateEvent, deleteEvent} from "../../actions/eventActions";
import PropTypes from "prop-types";

// core components
import PanelHeader from "components/PanelHeader/PanelHeader.jsx";
import {Bar, Line} from "react-chartjs-2";
import {NotificationContainer, SuccessNotification} from "../../components/Notifications/Notifications";
import {LoadingScreenSpinner} from "../../components/LoadingScreen/LoadingScreen";

import {
    dashboardAllProductsChart,
    dashboard24HoursPerformanceChart
} from "variables/charts.jsx";


class EventRead extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            _id:            "",
            eventName:      "",
            eventStart:     "",
            eventEnd:       "",
            eventLocation:  "",
            eventAge:       "",
            ticketVariants: "",
            user:           "",
            isLoaded:       false
        }
    }

    componentWillMount() {
        this.props.getUser("5e8b3e31b1fb5d3ece24b2a9");
        this.props.getEvent(this.props.match.params.id);
    }

    // set userdata
    componentDidUpdate() {
        if (!this.props.eventData.loading && this.state.isLoaded === false) {

            const {event} = this.props.eventData;

            console.log(event);

            this.setState({
                _id:            event._id,
                eventName:      event.eventName,
                eventStart:     event.eventStart,
                eventEnd:       event.eventEnd,
                eventLocation:  event.eventLocation,
                eventAge:       event.eventAge,
                ticketVariants: event.ticketVariants,
                user:           event.user,
                isLoaded:       true,
            });
        }
    }

    /**
     * Updates event
     */
    updateEvent() {
        const {isLoaded, ...body} = this.state;

        this.props.updateEvent(this.state._id, body);
        SuccessNotification("Event updated!");
    }

    /**
     * Handles input changes
     * @param e
     */
    handleChange(e) {
        const value = e.target.value;
        this.setState({
            ...this.state,
            [e.target.name]: value
        });

        console.log(this.state)
    }

    render() {

        if (this.props.userData.loading && this.props.eventData.loading) {
            return (
                <LoadingScreenSpinner/>
            )
        } else {

            return (
                <>
                    {console.log(this.state)}
                    <PanelHeader size="sm"/>
                    <NotificationContainer/>

                    <div className="content">
                        <Row>
                            <Col xs={12} md={6}>
                                <Card>
                                    <div className="">
                                        <img alt="..." src={require("assets/img/events/placeholder.png")}/>
                                    </div>
                                    <CardBody>
                                        <div className="author">
                                            <h5 className="title">{this.state.eventName}</h5>
                                            <p className="description">{new Date(this.state.eventStart).toLocaleString() + " - " + new Date(this.state.eventEnd).toLocaleString()}</p>
                                        </div>
                                    </CardBody>

                                    <hr/>

                                    <div className="button-container">
                                        <Button
                                            className="btn-neutral btn-icon btn-round"
                                            color="default"
                                            target={"_blank"}
                                            href={"../../shop/event/" + this.state._id}
                                            size="lg"
                                        >
                                            <i className="now-ui-icons files_box"/>
                                        </Button>
                                    </div>
                                </Card>
                            </Col>


                            <Col xs={12} md={6}>
                                <Card>
                                    <CardHeader>
                                        <CardTitle tag="h4">Update Event</CardTitle>
                                        <p className="category">You can update Event information here.</p>
                                    </CardHeader>

                                    <CardBody>
                                        <Form>
                                            <Row>
                                                <Col md="12">
                                                    <FormGroup>
                                                        <label>Event name</label>
                                                        <Input
                                                            name="eventName"
                                                            placeholder="Event name"
                                                            value={this.state.eventName}
                                                            type="text"
                                                            onChange={(e) => {
                                                                this.handleChange(e)
                                                            }}
                                                        />
                                                    </FormGroup>
                                                </Col>
                                                <Col md="12">
                                                    <FormGroup>
                                                        <label>
                                                            Event start
                                                        </label>
                                                        {console.log(new Date(this.state.eventStart).toLocaleString())}
                                                        <Input
                                                            disabled
                                                            name="eventStart"
                                                            placeholder="Event start"
                                                            value={new Date(this.state.eventStart).toLocaleString()}
                                                            onChange={(e) => {
                                                                this.handleChange(e)
                                                            }}
                                                        />
                                                    </FormGroup>
                                                </Col>
                                                <Col md="12">
                                                    <FormGroup>
                                                        <label>Event end</label>
                                                        <Input
                                                            disabled
                                                            name="eventEnd"
                                                            placeholder="Event end"
                                                            value={new Date(this.state.eventStart).toLocaleString()}
                                                            onChange={(e) => {
                                                                this.handleChange(e)
                                                            }}
                                                        />
                                                    </FormGroup>
                                                </Col>
                                            </Row>
                                            <Row>
                                                <Col md="12">
                                                    <FormGroup>
                                                        <label>Location</label>
                                                        <Input
                                                            name="eventLocation"
                                                            placeholder="Location"
                                                            value={this.state.eventLocation}
                                                            type="text"
                                                            onChange={(e) => {
                                                                this.handleChange(e)
                                                            }}
                                                        />
                                                    </FormGroup>
                                                </Col>
                                                <Col md="12">
                                                    <FormGroup>
                                                        <label>Minimum age</label>
                                                        <Input
                                                            name="eventAge"
                                                            placeholder="Minimum age"
                                                            value={this.state.eventAge}
                                                            type="number"
                                                            onChange={(e) => {
                                                                this.handleChange(e)
                                                            }}
                                                        />
                                                    </FormGroup>
                                                </Col>
                                            </Row>

                                            <button className="btn-round btn btn-success"
                                                    style={{fontSize: "1em", position: "relative", width: "100px"}}
                                                    onClick={(e) => {
                                                        e.preventDefault();
                                                        this.updateEvent();
                                                    }}>
                                                Update
                                            </button>
                                        </Form>
                                    </CardBody>
                                </Card>
                            </Col>
                        </Row>

                        <Row>
                            <Col xs={12} md={6}>
                                <Card>
                                    <CardHeader>
                                        <CardTitle tag="h4">Check-in</CardTitle>
                                        <h5 className="card-category">Check-in Statistics</h5>
                                    </CardHeader>
                                    <CardBody>
                                        <div className="chart-area">
                                            <Bar
                                                data={dashboard24HoursPerformanceChart.data}
                                                options={dashboard24HoursPerformanceChart.options}
                                            />
                                        </div>
                                    </CardBody>
                                </Card>
                            </Col>

                            <Col xs={12} md={6}>
                                <Card>
                                    <CardHeader>
                                        <CardTitle tag="h4">Orders</CardTitle>
                                        <h5 className="card-category">Order Statistics</h5>
                                    </CardHeader>
                                    <CardBody>
                                        <div className="chart-area">
                                            <Line
                                                data={dashboardAllProductsChart.data}
                                                options={dashboardAllProductsChart.options}
                                            />
                                        </div>
                                    </CardBody>
                                </Card>
                            </Col>

                            <Col xs={12}>
                                <Card>
                                    <CardHeader>
                                        <CardTitle tag="h4">Ticket Variants</CardTitle>
                                    </CardHeader>
                                    <CardBody>
                                        <Table responsive>
                                            <thead className="text-primary">
                                            <tr>
                                                <th>Name</th>
                                                <th>Price</th>
                                                <th>Available</th>
                                                <th>Persons</th>
                                                <th>Orders</th>
                                                <th className="text-center"> Details</th>
                                            </tr>
                                            </thead>
                                            <tbody>
                                            {this.state.ticketVariants ? this.state.ticketVariants.map((value, index) => (
                                                <tr key={index}>
                                                    <td>{value.description}</td>
                                                    <td>€{value.price}</td>
                                                    <td>{value.availability ? "On sale" : "Not on sale"}</td>
                                                    <td>{value.personsAmount}</td>
                                                    <td>0</td>
                                                    <td className="text-center">
                                                        <a
                                                            href={"./ticketvariant/" + this.state._id + "/" + value._id}
                                                            className="btn-round btn-outline-default btn-icon btn btn-default"
                                                            style={{margin: "0px 5px"}}>
                                                            <i className="now-ui-icons loader_gear"></i>
                                                        </a>

                                                        <a href="#"
                                                           className="btn-round btn-outline-primary btn-icon btn btn-default"
                                                           style={{margin: "0px 5px"}} onClick={(e) => {
                                                            e.preventDefault();
                                                            this.props.deleteEvent(value._id);
                                                            this.props.getUser("5e8b3e31b1fb5d3ece24b2a9");
                                                        }}>
                                                            <i className="now-ui-icons ui-1_simple-delete"></i>
                                                        </a>
                                                    </td>
                                                </tr>)) : ""}
                                            <tr>
                                                <td colSpan="5"></td>
                                                <td className="text-center">
                                                    <a href="#"
                                                       className="btn-round btn-outline-default btn-icon btn btn-default"
                                                       style={{margin: "0px 5px"}} onClick={(e) => {
                                                        e.preventDefault();
                                                        this.props.getEvent(this.props.match.params.id);
                                                    }}>
                                                        <i className="now-ui-icons loader_refresh"></i>
                                                    </a>
                                                    <a href={"../event/ticketvariant/create/" + this.props.match.params.id}
                                                       className="btn-round btn-outline-success btn-icon btn btn-default"
                                                       style={{margin: "0px 5px"}}>
                                                        <i className="now-ui-icons ui-1_simple-add"></i>
                                                    </a>
                                                </td>
                                            </tr>
                                            </tbody>
                                        </Table>
                                    </CardBody>
                                </Card>
                            </Col>
                        </Row>
                    </div>
                </>
            );
        }
    }
}

EventRead.propTypes = {
    getEvent:    PropTypes.func.isRequired,
    addEvent:    PropTypes.func.isRequired,
    updateEvent: PropTypes.func.isRequired,
    deleteEvent: PropTypes.func.isRequired,
    getUser:     PropTypes.func.isRequired,
    userData:    PropTypes.object.isRequired,
    eventData:   PropTypes.object.isRequired
};

const mapStateToProps = (state) => ({
    userData:  state.userData,
    eventData: state.eventData,
});

export default connect(mapStateToProps, {getUser, addEvent, getEvent, updateEvent, deleteEvent})(EventRead);
