import {LOGIN_AUTH, LOGOUT_AUTH, MAIL_AUTH, VERIFY_AUTH, LOADING_AUTH} from "../actions/types";

const initialState = {
    storageSession: localStorage.getItem('tickit-token'),
    token: {},
    session:    {},
    loading: false,
    mailSent: false,
    authVerified: false
};

export default function (state = initialState, action) {
    switch (action.type) {
        case LOGIN_AUTH:
            return {
                ...state,
                token: action.payload,
                loading: false
            };
        case LOGOUT_AUTH:
            return {
                ...state,
                session: {},
                authVerified: false,
                loading: false
            };
        case MAIL_AUTH:
            return {
                ...state,
                mailSent: true,
                loading: false
            };
        case VERIFY_AUTH:
            return {
                ...state,
                session: action.payload,
                authVerified: true,
                loading: false
            };
        case LOADING_AUTH:
            return {
                ...state,
                loading: true
            };
        default:
            return state;
    }
}

