import axios from "axios";
import {GET_USERS, GET_USER, ADD_USER, UPDATE_USER, DELETE_USER, LOADING_USER} from "./types";

export const getUsers = () => dispatch => {
    dispatch(setUserLoading());
    axios.get("/users/").then(res =>
        dispatch({
            type:    GET_USERS,
            payload: res.data
        }))
};

export const getUser = (id) => dispatch => {
    dispatch(setUserLoading());
    axios.get("/users/" + id).then(res =>
        dispatch({
            type:    GET_USER,
            payload: res.data[0]
        }))
};

export const addUser = (body) => dispatch => {
    dispatch(setUserLoading());
    axios.get("/users/signup", body).then(res =>
        dispatch({
            type:    ADD_USER,
            payload: res.data[0]
        }))
};

export const updateUser = (id, body) => dispatch => {
    dispatch(setUserLoading());
    axios.post("/users/update/" + id, body).then(res =>
        dispatch({
            type:    UPDATE_USER,
            payload: res.data
        }))
        .catch(err => {
            console.log(err);
        })
};

export const deleteUser = (id) => dispatch => {
    dispatch(setUserLoading());
    axios.delete("/users/" + id).then(res =>
        dispatch({
            type:    DELETE_USER,
            payload: res.data[0]
        }))
};

export const setUserLoading = () => {
    return {
        type: LOADING_USER
    }
};
